@import "_header";
@import "_footer";
.page{
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  &__wrapper{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }
}

.screen{
  flex: 1 0 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: $black;
  overflow: hidden;
  &__content{
    opacity: 0;
    transition: opacity 1.8s ease 0s;
  }
}

.swiper-slide-active{
  .screen__content{
    opacity: 1;
  }
}


