@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.25em;
    height: 2.5625em;
    border-radius: 0.5em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .875em;
    }
}

.m-btn-blue{
    background-color: $primary;
    color: $white;
    img{
        width: 1.25em;
        margin: 0 .5em;
        &:first-child{
            margin-left: 0;
        }
        &:last-child{
            margin-right: 0;
        }
    }
}