.text{
  line-height: 171%;
  p,li{
    font-size: .875em;
    line-height: 171%;
  }
  ol,ul,
  p{
    @include lastM(0 0 1.25em);
  }
  ol,ul{
    padding-left: .5em;
    li{
      padding-left: 1em;
      &:before{
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
  ol {
    counter-reset: list;
    li {
      counter-increment: list;
      &:before {
        content: counter(list) '. ';
      }
    }
  }
  ul{
    li{
      position: relative;
      &:before{
        content: '•';
      }
    }
  }
}


@media screen and (max-width: 576px){
  .text {
    line-height: 167%;
    p, li {
      font-size: .75em;
      line-height: 167%;
    }
  }
}
