.text-center{
  text-align: center;
}

.caps-text{
  text-transform: uppercase;
}

.underline-text{
  text-decoration: underline;
}

.h1{
  font-size: 1.5625em;
  line-height: 120%;
  font-weight: 700;
  font-family: $font2;
}

.h2{
  font-size: 1.375em;
  line-height: 145%;
  font-family: $font2;
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 139%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text16{
  font-size: 1em;
  line-height: 143%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 120%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.gray-text{
  color: $gray;
}


@media screen and (max-width: 576px){
  .h1{
    font-size: 1em;
    line-height: 125%;
  }

  .text20-mob{
    font-size: 1.25em;
  }

  .text19-mob{
    font-size: 1.1875em;
  }

  .text18-mob{
    font-size: 1.125em;
  }

  .text17-mob{
    font-size: 1.0625em;
  }

  .text16-mob{
    font-size: 1em;
  }

  .text15-mob{
    font-size: .9375em;
  }

  .text14-mob{
    font-size: .875em;
  }

  .text13-mob{
    font-size: .8125em;
  }

  .text12-mob{
    font-size: .75em;
  }

  .text11-mob{
    font-size: .6875em;
  }

  .text10-mob{
    font-size: .625em;
  }

}
