.info{
  padding-top: 5.5em;
  background-image: url(../img/info-bg.svg);
  &__text{
    text-align: center;
    width: 56em;
    max-width: 100%;
    margin: 0 auto;
    color: $gray;
  }
  &__img{
    margin-top: auto;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}

.info2{
  background-image: url(../img/info-bg3.svg);
}

.info3{
  background-image: url(../img/info-bg5.svg);
}

.contacts{
  padding: 3.25em 1em 2em;
  justify-content: center;
  background-image: url(../img/contacts-bg.webp);
  background-size: cover;
  background-position: center;
  &__inner{
    width: 46em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__text{
    text-align: center;
    @include grid100gap(1.5em);
  }
  &__presentation{
    margin-top: 2.44em;
    text-align: center;
    .text16{
      margin-bottom: .25em;
    }
  }
  &__items{
    margin-top: 3.75em;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__c{
    display: none;
  }
}

.contacts-form{
  margin-top: 3.5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.0625em));
    gap: 2.125em;
  }
  &__bottom{
    margin-top: 2.125em;
  }
}

.contact-item{
  display: flex;
  align-items: center;
  font-family: $font2;
  text-transform: uppercase;
  &--presentation{
    display: none;
  }
  svg{
    width: 2.3125em;
    margin-right: .875em;
  }
  &:hover{
    path{
      fill: $primary;
    }
  }
}


@media screen and (max-width: 576px){
  .info{
    padding-top: 5.75em;
    background-image: url(../img/info-bg2.svg);
    &__text{
      text-align: left;
      padding: 0 2em 0 1.375em;
      @include grid100gap(.875em);
    }
    &__title{
      font-size: 1.375em;
      line-height: 145%;
      font-family: $font2;
      color: $white;
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .info2{
    background-image: url(../img/info-bg4.svg);
    .info__text{
      margin-top: auto;
      padding-bottom: 2em;
    }
  }

  .info3{
    background-image: url(../img/info-bg6.svg);
    .info__text{
      margin-top: auto;
      margin-bottom: -2em;
    }
  }

  .contacts{
    padding: 2em 1.43em 6em;
    background-image: url(../img/contacts-bg2.webp);
    background-size: 100% auto;
    background-position: center bottom 0;
    &__presentation,
    &__desc{
      display: none;
    }
    &__bottom{
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      padding-bottom: .75em;
    }
    &__items{
      justify-content: center;
      margin: 0 0 1em;
    }
    &__c{
      display: block;
      text-align: center;
      font-size: .75em;
      line-height: 150%;
      text-transform: uppercase;
      color: $gray;
    }
  }

  .contacts-form{
    margin-top: 1.32em;
    &__grid{
      grid-template-columns: 100%;
      gap: .5em;
    }
    &__bottom{
      @include grid100gap(.625em);
      margin-top: 1em;
    }
    &__btn{
      order: -1;
    }
  }

  .contact-item{
    margin: 0 .5em;
    display: flex;
    svg{
      width: 2em;
      margin: 0;
    }
    span{
      display: none;
    }
  }
}