@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
._flex,
._flex-between,
._flex-center{
  display: flex;
  align-items: center;
  &._start{
    align-items: unset;
  }
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

._mt48{
  margin-top: 3em;
}

._mt32{
  margin-top: 2em;
}

._mt24{
  margin-top: 1.5em;
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.12);
    pointer-events: none;
    border-radius: inherit;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2em;
    height: 2em;
    @include lastM(0 1em 0 0);
    @include center;
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}


