.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .9375em;
    line-height: 140%;
    margin-bottom: .375em;
    span{
      color: $primary;
    }
  }
  input,
  textarea{
    border-radius: .5em;
    border: 1px solid #6A84A7;
    padding: 0 1em;
    height: 3.25em;
    width: 100%;
    font-size: 1em;
    background-color: #0B0B0C;
    color: $white;
    &::placeholder {
      color: $gray;
    }
  }
  textarea{
    padding-top: .8em;
    padding-bottom: .8em;
    height: 5.75em;
    resize: none;
  }
  &__field{
    position: relative;
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}

.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 1.5em;
    min-height: 1em;
    span{
      font-size: .875em;
      line-height: 100%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: .125em;
      border: 1px solid $white;
      width: 1em;
      height: 1em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: .875em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .25em;
      background-image: url(../img/check.svg);
    }
  }
}


@media screen and (max-width: 576px){
  .fg{
    input,
    textarea{
      border-radius: .57em;
      padding: 0 1.2em;
      height: 3.5em;
      font-size: .875em;
    }
    textarea{
      padding-top: .92em;
      padding-bottom: .92em;
      height: 6.57em;
    }
  }

  .m-check{
    label{
      display: flex;
      align-items: center;
      span{
        font-size: .6875em;
      }
      &:before{
        top: 0;
      }
    }
  }
}
