.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .7;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: $white;
        color: $black;
        width: 40.625em;
        max-width: calc(100% - 4em);
        position: relative;
        z-index: 3;
        padding: 2.69em;
        display: inline-block;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__close{
        position: absolute;
        height: 100%;
        right: 1.25em;
        top: 0;
        padding-top: 1.25em;
    }
    .close-btn{
        top: 1.25em;
        position: sticky;
    }
}

.close-btn{
    width: 1.125em;
    @include center;
    img{
        width: 100%;
    }
}



@media screen and (max-width: 576px){
    .modal{
        padding-top: 1.69em;
        &:after{
            display: none;
        }
        &__dialog {
            padding: 2em 2.7em 2em 2em;
        }
        &__close{
            right: 1em;
        }
    }
}
