.solution{
  justify-content: center;
  padding: 3.375em;
  &__text{
    max-width: 29.375em;
    @include grid100gap(1em);
    transform: translateY(1em);
    position: relative;
    z-index: 2;
  }
  &__title{
    padding-right: 1em;
  }
  &__video{
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    &:after {
      content: '';
      background: url(../img/main-bg-pattern.png);
      position: absolute;
      bottom: 0;
      top: 0;
      left: 0;
      right: 0;
      margin: auto;
      z-index: 0;
    }
    video{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
}

.modules{
  padding: 5em 3.31em;
  justify-content: flex-end;
  background-image: url(../img/modules-bg.svg);
  &__bg{
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    max-width: 74.4375em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__top{
    max-width: 32em;
  }
}

.modules-slider{
  margin-top: 3.82em;
  @media screen and (min-width: 577px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(2, 18.8125em);
        gap: 3em 3.3125em;
        transform: unset !important;
      }
      &-slide{
        width: 100% !important;
        &-duplicate{
          display: none !important;
        }
      }
      &-pagination{
        display: none;
      }
    }
  }
}

._full{
  grid-column: 1/-1;
}

.module-item{
  &__bottom{
    margin-top: .59em;
  }
  &__icon{
    width: 2.25em;
    margin-right: .625em;
  }
}


@media screen and (max-width: 576px){
  .solution{
    justify-content: unset;
    padding: 8em 2.25em 0 1.544em;
    &__text{
      gap: .875em;
      transform: unset;
    }
    &__desc{
      max-width: 15.5em;
      color: $gray;
    }
    &__video{
      height: auto;
      width: 180%;
      margin-left: -40%;
    }
  }

  .modules{
    padding: 6.75em 1.375em 3em;
    justify-content: space-between;
    background-image: url(../img/modules-bg2.svg);
    &:after{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 8.625em;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 0%, #000 100%);
    }
    &__top{
      max-width: 17em;
    }
    &__bg{
      bottom: -8.375em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .modules-slider{
    margin: 2.69em auto 0;
    width: 11.5625em;
    z-index: 2;
    .swiper{
      &-container{
        width: calc(100% + .375em);
      }
      &-wraper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        padding-right: .375em;
      }
    }
  }

  .module-item{
    border-radius: 0.125em;
    background: rgba(255, 255, 255, 0.14);
    padding: 1.91em .58em .5em;
    height: 100%;
    text-align: center;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__icon{
      width: 1.375em;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: .44em;
    }
    &__bottom{
      order: -1;
      margin: 0 0 .25em;
    }
    &__desc{
      font-size: .875em;
      line-height: 143%;
    }
    &__title{
      font-size: .5em;
      line-height: 120%;
      text-transform: uppercase;
      color: #828282;
      flex-flow: $font2;
      font-weight: 600;
    }
  }
}