.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 5.625em;
  padding: 0 2.25em;
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 8.5em;
    position: relative;
    z-index: 12;
    img{
      width: 100%;
      display: block;
    }
  }
  &._open{
    .burger{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
    .menu{
      transform: translateX(0);
      &__bg{
        display: block;
      }
    }
  }
}

.burger{
  width: 1.375em;
  height: 1.75em;
  position: relative;
  z-index: 12;
  img{
    @include centerAbsolute;
    width: 100%;
    transition: all .3s;
  }
  ._view2{
    opacity: 0;
  }
}

.menu{
  transition: all .5s;
  transform: translateX(110%);
  position: fixed;
  top: 0;
  right: 0;
  height: $vh;
  background-color: rgba($black, .8);
  width: 21.75em;
  z-index: 11;
  &__bg{
    display: none;
  }
  &__scroll{
    overflow-y: auto;
    height: 100%;
    padding: 14em 1em 6.93em;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .socials{
    margin-top: auto;
  }
  &__bg{
    opacity: 0;
    z-index: 9;
    display: none;
  }
}

.nav{
  text-transform: uppercase;
  font-family: $font2;
  text-align: center;
  font-weight: 700;
  padding-bottom: 2em;
  ul{
    @include grid100gap(2.28em);
  }
  a{
    font-size: 1em;
    line-height: 200%;
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: 0 1.44em;
  }

  .menu{
    width: 100%;
    background: $black;
    &__scroll{
      padding-top: 12em;
      padding-bottom: 3.19em;
    }
  }
}
