.main{
  align-items: center;
  justify-content: center;
  text-align: center;
  position: relative;
  &__canvas{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  &__inner{
    margin-top: -1.25em;
    width: 23em;
    max-width: 100%;
    position: relative;
    z-index: 2;
  }
  &__logo{
    width: 16.75em;
    margin: 0 auto 1.81em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    color: #B5B5B5;
    opacity: .8;
  }
  &__next{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 3.75em;
    width: 1.5em;
    @include center;
    z-index: 2;
    background-color: transparent;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

._radial-bg{
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top;
}

.advantages{
  padding: 6.5em 5.375em 0;
  background-image: url(../img/advantages-bg.svg);
  &__img{
    width: 49.25em;
    margin: auto auto 0;
    padding-top: 2em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__img,
  &__title,
  .advantages-slider{
    position: relative;
    z-index: 2;
  }
}

.swiper-pagination{
  @include center;
  position: static;
  margin-top: 1.75em;
  &-bullet{
    width: .5em;
    height: .5em;
    border: 1px solid #B5B5B5;
    opacity: 1;
    background-color: transparent;
    &-active{
      width: .9em;
      border-radius: .85em;
      background-color: $white;
      border-color: $white;
    }
  }
}

.advantages-slider{
  margin-top: 3.45em;
  @media screen and (min-width: 577px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(3, 16.875em);
        justify-content: space-between;
        transform: unset !important;
      }
      &-slide{
        width: 100% !important;
        &-duplicate{
          display: none !important;
        }
      }
      &-pagination{
        display: none;
      }
    }
  }
}

.advantage-item{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__icon{
    height: 2.25em;
    margin-bottom: 1.17em;
  }
  &__text{
    span{
      color: $white;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__inner{
      margin-top: -.5em;
      width: 13.125em;
    }
    &__logo{
      width: 11.25em;
      margin-bottom: 1.375em;
    }
    &__next{
      bottom: 3.125em;
    }
  }

  ._radial-bg{
    background-position: center top 14em;
    align-items: center;
  }

  .advantages{
    padding: 8.125em 0 0;
    background-image: url(../img/advantages-bg2.svg);
    &__img{
      height: 16.125em;
      position: relative;
      width: 100%;
      img{
        width: auto;
        height: 14.125em;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }

  .advantages-slider{
    margin: 2.69em auto 0;
    width: 19em;
    .swiper{
      &-container{
        width: calc(100% + .625em);
      }
      &-wraper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        padding-right: .625em;
      }
    }
  }

  .advantage-item{
    border-radius: 0.125em;
    background: rgba(255, 255, 255, 0.14);
    padding: 1em 1.56em;
    height: 100%;
    &__icon{
      margin-bottom: 1em;
    }
    &__text{
      letter-spacing: .02em;
    }
  }
}
